<template>
  <div class="setting-container">
    <div class="list">
      <router-link to="/transcation.html" class="item">
        <span class="name">交易记录</span>
        <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
      </router-link>
      <router-link to="/likeDetail.html" class="item">
        <span class="name">“喜欢”使用明细</span>
        <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
      </router-link>
      <p class="item" @click="showPicker = true">
        <span class="name">感情状态</span>
        <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
      </p>
      <router-link to="/userDeal.html" class="item">
        <span class="name">用户须知</span>
        <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
      </router-link>
      <router-link to="/userDeal.html" class="item">
        <span class="name">关于我们</span>
        <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
      </router-link>
    </div>

    <a href="javascript:;" @click="logout" class="logout-btn">退出</a>

    <van-popup v-model="showPicker" position="bottom">
      <van-picker
        show-toolbar
        :visible-item-count="3"
        :columns="statusColumns"
        @cancel="showPicker = false"
        @confirm="onConfirm"
      />
    </van-popup>

  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'setting',
  data () {
    return {
      showPicker: false
    }
  },
  computed: {
    ...mapState({
      statusMap: 'statusMap'
    }),
    statusColumns () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      return arr
    }
  },
  methods: {
    onConfirm (val) {
      console.log(val)
      this.showPicker = false
    },
    logout () {
      location.href = location.origin
    }
  }
}
</script>

