<template>
  <div class="set-data-container">
    <MyHeader :title="title" :back="backStep"/>
    <div class="components">
      <div class="set-sexy" v-if="step === 1">
        <p class="who">你是</p>
        <div class="choose-con">
          <span class="sexy" v-for="item in sexArr" :key="item.value" :class="[item.value == '1' ? 'male' : 'female', {'selected': tempData.sex == item.value }]" @click="selectItem(item.value, 1)"></span>
        </div>
      </div>
      <div class="set-education" v-else-if="step === 2">
        <p class="who">你的学历</p>
        <div class="choose-con">
          <a class="content-btn" href="javascript:;"
            v-for="(item, index) in educationData"
            :key="index"
            :class="{'selected': tempData.education === item.key }"
            @click="selectItem(item.key, 2)">{{item.value}}</a>
        </div>
      </div>
      <div class="set-birthday" v-else-if="step === 3">
        <p class="who">你的生日</p>
        <div class="choose-con">
          <van-datetime-picker
            v-model="datepicker.currentDate"
            type="date"
            :min-date="datepicker.minDate"
            :max-date="datepicker.maxDate"
            :visible-item-count="1"
            :formatter="formatter"
          />
          <p class="select-date-tip">上下滑动选择日期</p>
          <p class="cautious">选择后不可更改哦，请谨慎选择</p>
          <a class="content-btn selected" href="javascript:;" @click="selectItem(datepicker.currentDate, 3)">下一步</a>
        </div>
      </div>
      <div class="set-tall" v-else-if="step === 4">
        <p class="who">你的身高</p>
        <div class="choose-con">

          <van-picker :columns="tallArray" :visible-item-count="1" :default-index="tallDefaultIndex" ref="tallPicker" @change="tallChange"/>

          <p class="select-date-tip">上下滑动选择身高</p>
          <p class="cautious">选择后不可更改哦，请谨慎选择</p>
          <div class="deals" @click="isAgreeDeal = !isAgreeDeal">
            <span class="checkbox"
              :class="{'checked': isAgreeDeal, 'focus': isFocus}"
            >
              <img src="~@/assets/img/tick.png" alt="" class="tick" />
            </span>
            <span class="word">阅读并同意</span>
            <router-link class="deal-link" to="/userDeal.html" @click.native.stop="">《Hello部落用户协议》</router-link>
          </div>
          <a class="content-btn selected" href="javascript:;" @click="complete">完成</a>
        </div>
      </div>
    </div>
    <p class="cautious" v-if="step === 1 || step === 2">选择后不可更改哦，请谨慎选择</p>
  </div>
</template>

<script>
import MyHeader from '@/components/common/myHeader'
import { mapState, mapGetters, mapMutations } from 'vuex'
import { setCookie, getCookie, delCookie } from '@/utils/tools'

export default {
  name: 'setUserData',
  components: {
    MyHeader
  },
  data () {
    return {
      title: '',
      step: 1,
      // 设置默认值
      tempData: {
        sex: 1,
        high: 170
      },
      educationData: [
        {
          key: 9,
          value: '博士及以上'
        },
        {
          key: 6,
          value: '硕士'
        },
        {
          key: 5,
          value: '本科'
        },
        {
          key: 4,
          value: '大专'
        },
        {
          key: 8,
          value: '大专以下'
        }
      ],
      datepicker: {
        minDate: new Date(1919, 1, 1),
        maxDate: new Date(),
        currentDate: new Date()
      },
      tallArray: Array.from(Array(150), (v, k) => k + 70 + 'cm'),
      tallDefaultIndex: 100,
      isAgreeDeal: true,
      isFocus: false
    }
  },
  computed: {
    ...mapState({
      userData: 'userData',
      sexMap: 'sexMap'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    sexArr () {
      let arr = []
      for (let i in this.sexMap) {
        arr.push({
          value: i,
          text: this.sexMap[i]
        })
      }
      return arr
    }
  },
  watch: {
    step (val) {
      switch (parseInt(val)) {
        case 1:
          this.title = '选择性别'
          break
        case 2:
          this.title = '选择学历'
          break
        case 3:
          this.title = '选择年龄'
          break
        case 4:
          this.title = '选择身高'
          break
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    backStep () {
      if (this.step === 1) {
        history.go(-1)
      } else {
        this.step = this.step - 1

        setCookie('tempUserData', JSON.stringify({ ...this.tempData, ...{ step: this.step } }))
      }
    },
    formatter (type, value) {
      if (type === 'year') {
        return `${value}年`
      } else if (type === 'month') {
        return `${value}月`
      } else if (type === 'day') {
        return `${value}日`
      }
      return value
    },
    selectItem (value, step) {
      switch (step) {
        case 1:
          this.tempData.sex = value
          break
        case 2:
          this.tempData.education = value
          break
        case 3:
          this.tempData.birthday = value
          break
        case 4:
          this.tempData.high = value
          break
      }
      setCookie('tempUserData', JSON.stringify({ ...this.tempData, ...{ step: this.step } }))

      if (this.step >= 4) {
        return
      }
      this.step += 1
    },
    tallChange (picker, value, index) {
      this.selectItem(parseInt(value), 4)
    },
    complete () {
      if (!this.isAgreeDeal) {
        this.$toast('请先勾选用户协议')
        this.isFocus = true
        return
      }

      let date = new Date(this.tempData.birthday)
      this.tempData.birthday = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
      this.tempData.currentMemberId = this.userId
      // 更新状态至已设置前置资料
      this.tempData.status = 4

      this.$root.openLoading()
      this.$http(`/api/member/save`,
        this.tempData
      ).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.updateUserData(this.tempData)
          delCookie('tempUserData')
          this.$router.replace('/account.html')
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$toast(err)
        this.$root.closeLoading()
      })
    }
  },
  mounted () {
    if (this.userData.id) {
      this.tempData.sex = this.userData.sex
    }
    const temp = getCookie('tempUserData')
    if (temp) {
      let { step, ...data } = JSON.parse(temp)
      this.step = step
      this.tempData = { ...this.tempData, ...data }
      if (data.birthday) {
        this.datepicker.currentDate = new Date(data.birthday)
      }
      if (data.high) {
        this.tallDefaultIndex = parseInt(data.high) - 70
      } else {
        this.tempData.high = 170
      }
    }
  }
}
</script>

