/**
 * localStorage的读取、写入和删除操作模块
 * @module Storage
 */

export default {
  save: (key, jsonData) => {
    localStorage.setItem(key, JSON.stringify(jsonData))
    return jsonData
  },
  load: key => {
    let record = null
    try {
      record = JSON.parse(localStorage.getItem(key))
    } catch (err) {}
    return record
  },
  remove: key => {
    'use strict'
    localStorage.removeItem(key)
  },
  saveOnday: (key, jsonData, expirationDay = 15) => {
    // 默认缓存15天
    let expirationMS = expirationDay * 24 * 60 * 60 * 1000
    let record = {
      value: JSON.stringify(jsonData),
      timestamp: new Date().getTime() + expirationMS
    }
    localStorage.setItem(key, JSON.stringify(record))
    return jsonData
  },
  saveOntime: (key, jsonData, expirationMinute = 30) => {
    // 默认缓存半个小时
    let expirationMS = expirationMinute * 60 * 1000
    let record = {
      value: JSON.stringify(jsonData),
      timestamp: new Date().getTime() + expirationMS
    }
    localStorage.setItem(key, JSON.stringify(record))
    return jsonData
  },
  loadOntime: key => {
    let record = JSON.parse(localStorage.getItem(key))
    if (!record) {
      return false
    }
    return (
      new Date().getTime() < record.timestamp && JSON.parse(record.value)
    )
  }
}
