<template>
  <header class="header">
    <slot name="nav-left">
      <div class="btn-con">
        <a href="javascript:;" @click="goBack" class="header-btn"><img src="~@/assets/img/back-icon.png" class="back-icon" /></a>
      </div>
    </slot>
    <h1 class="header-title">{{myTitle}}</h1>
    <slot name="nav-right"></slot>
  </header>
</template>

<script>
export default {
  name: 'myHeader',
  props: ['title', 'back'],
  computed: {
    myTitle () {
      return this.title || this.$route.meta.title || 'Hello 部落'
    }
  },
  methods: {
    goBack () {
      if (this.back) {
        this.back()
      } else {
        history.go(-1)
      }
    }
  }
}
</script>

