    </div>

    <div class="line"></div>

    <div class="input-form">
      <div class="input-item strong select-item">
        <span class="name"><b>婚姻状态</b></span>
        <p class="input" @click="triggerPicker(3)">
          <span class="value">{{statusMap[infoData.singleStatus]}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
      <div class="input-item strong select-item">
        <span class="name"><b>车房情况</b></span>
        <p class="input" @click="triggerPicker(4)">
          <span class="value">{{assetsMap[infoData.richesInfo]}}</span>
          <img src="~@/assets/img/arrow-enter.png" alt="" />
        </p>
      </div>
    </div>

    <div class="line"></div>

    <div class="input-form">
      <div class="input-item strong">
        <span class="name"><b>注册邀请码（非必填）</b></span>
