    <div class="line"></div>

    <div class="input-form textarea-form">
      <div class="textarea-con">
        <p class="title">自我描述</p>
        <textarea class="textarea" v-model="infoData.selfDescription" placeholder="可从性格，习惯等自我认知维度去描述" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.selfDescription.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title">家庭背景</p>
        <textarea class="textarea" v-model="infoData.familyInfo" placeholder="可从性格，习惯等自我认知维度去描述" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.familyInfo.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title">个人经历</p>
        <textarea class="textarea" v-model="infoData.personalExperience" placeholder="可从性格，习惯等自我认知维度去描述" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.personalExperience.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title">兴趣爱好</p>
        <textarea class="textarea" v-model="infoData.hobby" placeholder="可从性格，习惯等自我认知维度去描述" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.hobby.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title">爱情观</p>
        <textarea class="textarea" v-model="infoData.loveMind" placeholder="可从性格，习惯等自我认知维度去描述" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.loveMind.length}}/{{limitLength}}</p>
      </div>
      <div class="textarea-con">
        <p class="title">理想的另一半</p>
        <textarea class="textarea" v-model="infoData.ideaPartner" placeholder="可从性格，习惯等自我认知维度去描述" :maxlength="limitLength"></textarea>
        <p class="text-num">{{infoData.ideaPartner.length}}/{{limitLength}}</p>
      </div>
