    <div class="btns">
      <a href="javascript:;" class="btn draft-btn" @click="saveDraft">保存草稿</a>
      <a href="javascript:;" class="btn submit-btn" @click="handleSubmit">提交</a>
    </div>

    <van-dialog v-model="isShowDialog" confirmButtonText="知道了" @confirm="confirm" @closed="isShowDialog = false">
      <div id="submitted-dialog">
