        <p class="tips">豆子姐微信：doujie123</p>
        <img src="/static/img/wechat-person.png" alt="豆子姐微信" class="code" />
        <p class="effect">截图保存后微信扫描添加豆姐加速审核</p>
      </div>
    </van-dialog>

    <van-popup v-model="showPicker" position="bottom">
      <van-picker :columns="areaColumns" :show-toolbar="true" v-if="pickerType == 1" @change="handleAreachange" @confirm="handleAreaconfirm" @cancel="handleAreacancel"></van-picker>
      <van-picker v-else :columns="pickerData" :show-toolbar="true" @confirm="handlePickerConfirm"></van-picker>
    </van-popup>

    <van-popup v-model="showChatCodePop" position="bottom">
      <div class="upload-code-pop">
        <div class="pop-title">
          <p class="title">{{infoData.wechatPicId ? '你的微信二维码已上传' : ''}}</p>
          <a class="close-btn" href="javascript:;" @click="showChatCodePop = false">
            <img class="close-img" src="~@/assets/img/close.png"/>
          </a>
        </div>
        <template v-if="!infoData.wechatPicId">
          <dl class="tips">
            <dt class="tips-title">重要信息，请知悉</dt>
            <dd class="tips-item">1、请上传你的微信二维码截图，并在平台对你的服务期间保证可以用该二维码添加你的微信。</dd>
            <dd class="tips-item">2、上传即代表同意平台对你的二维码信息有权发送给平台其他希望获取你联系方式的用户，但最终微信添加是否通过取决于你。</dd>
          </dl>
          <a href="javascript:;" class="upload-btn common-bg">
            继续上传
            <input type="file" class="upload-input" accept="image/*" @change="uploadWebchatCode"/>
          </a>
        </template>
        <template v-else>
          <img class="code-img" :src="`/api/member/pic?id=${infoData.wechatPicId}&type=4&targetMemberId=${userId}&currentMemberId=${userId}`" alt="" />
          <a href="javascript:;" class="reupload-btn">
            重新上传
            <input type="file" class="upload-input" accept="image/*" @change="uploadWebchatCode"/>
          </a>
        </template>
      </div>
    </van-popup>

    <Clip v-model="isShowClip" :img="tempImg" @clipComplete="clipComplete"/>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import Clip from '@/components/common/clip'
import areaData from '@/assets/area.js'

import { isIOS, transferBytes } from '@/utils/tools'
const getRotate = () => {
  return import('@/utils/rotateImg.js')
}

export default {
  name: 'BaseInfo',
  components: {
    Clip
  },
  computed: {
    ...mapState({
      userData: 'userData',
      sexMap: 'sexMap',
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    salaryArr () {
      let arr = []
      for (let i in this.salaryMap) {
        arr.push({
          value: i,
          text: this.salaryMap[i]
        })
      }
      return arr
    },
    statusArr () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      return arr
    },
    assetsArr () {
      let arr = []
      for (let i in this.assetsMap) {
        arr.push({
          value: i,
          text: this.assetsMap[i]
        })
      }
      return arr
    }
  },
  data () {
    return {
      areaColumns: [
        {
          values: Object.keys(areaData)
        },
        {
          values: areaData['北京']
        }
      ],
      showPicker: false,
      pickerType: 1,
      pickerData: [],
      showChatCodePop: false,
      infoData: {
        coverPicList: null,
        name: '',
        nickName: '',
        isForeign: false, // 所选地区是否海外
        provice: '',
        locationCity: '',
        mobile: '',
        wechatPicId: '',
        school: '',
        job: '',
        company: '',
        companyVisible: 1,
        industry: '',
        // 年薪
        annualSalary: 1,
        annualSalaryVisible: 1,
        // 婚姻状态
        singleStatus: '',
        // 车房状态
        richesInfo: '',
        inviteCode: '',
        selfDescription: '',
        familyInfo: '',
        personalExperience: '',
        hobby: '',
        // 爱情观
        loveMind: '',
        // 理想的另一半
        ideaPartner: ''
      },
      limitLength: 200,
      isShowDialog: false,
      isShowClip: false,
      tempImg: ''
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    selectedImg (e) {
      let file = e.target.files[0]
      if (!/image\/\w+/.test(file.type)) {
        alert('不是图像类型')
        return
      }
      this.tempImg = file
      this.isShowClip = true
    },
    clipComplete (file) {
      this.isShowClip = false
      try {
        this.$root.openLoading()

        // 将文件转换成bytes后上传
        transferBytes(file).then(res => {
          this.$http('/api/member/upload/pic', {
            currentMemberId: this.userId,
            type: 1,
            pic: res
          }).then(res => {
            this.$root.closeLoading()
            if (res.code === '0000') {
              if (this.infoData.coverPicList) {
                this.infoData.coverPicList.push(res.data)
              } else {
                this.infoData.coverPicList = [res.data]
              }
            } else {
              this.tempImg = ''
              document.getElementById('select-file-btn').value = ''
              this.$toast(res.msg)
            }
          }, err => {
            this.$root.closeLoading()
            this.$toast(err)
            this.tempImg = ''
            document.getElementById('select-file-btn').value = ''
          })
        })
      } catch (err) {
        this.$root.closeLoading()
        this.$toast(err)
        this.tempImg = ''
        document.getElementById('select-file-btn').value = ''
      }
    },
    deleteImg (index) {
      this.infoData.coverPicList.splice(index, 1)
    },
    uploadWebchatCode (e) {
      let file = e.target.files[0]
      if (!/image\/\w+/.test(file.type)) {
        this.$toast('不是图像类型')
        e.target.value = ''
        return
      }
      if (isIOS()) {
        getRotate().then(({ rotatePhoto }) => {
          rotatePhoto(e.target.files[0], (res) => {
            this.setChatCode(res, e.target)
          })
        })
      } else {
        this.setChatCode(file, e.target)
      }
    },
    setChatCode (file, target) {
      // 将文件转换成bytes后上传
      transferBytes(file).then(res => {
        this.$http('/api/member/upload/pic', {
          currentMemberId: this.userId,
          type: 4,
          pic: res
        }).then(res => {
          if (res.code === '0000') {
            this.infoData.wechatPicId = res.data
            this.showChatCodePop = false
          } else {
            this.$toast(res.msg)
            target.value = ''
          }
        }, err => {
          this.$toast(err)
          target.value = ''
        })
      }, err => {
        this.$toast(err)
        target.value = ''
      })
    },
    /**
     * @description 触发picker选择器
     * @param type { Number }
     *    2: 年薪
     *    3: 婚姻状态
     *    4: 车房情况
     */
    triggerPicker (type) {
      this.pickerType = type
      this.showPicker = true

      switch (type) {
        case 2:
          this.pickerData = this.salaryArr
          break
        case 3:
          this.pickerData = this.statusArr
          break
        case 4:
          this.pickerData = this.assetsArr
          break
      }
    },
    handleAreacancel () {
      this.showPicker = false
    },
    handleAreachange (picker, res) {
      picker.setColumnValues(1, areaData[res[0]])
    },
    handleAreaconfirm (res) {
      this.showPicker = false
      this.infoData.provice = res[0]
      // 海外地区特殊处理
      if (res[0] === '海外') {
        this.infoData.isForeign = true
        this.infoData.provice = ''
        this.infoData.locationCity = ''
      } else {
        this.infoData.isForeign = false
        this.infoData.provice = res[0]
        this.infoData.locationCity = res[1]
      }
    },
    handlePickerConfirm (res) {
      this.showPicker = false
      switch (this.pickerType) {
        case 2:
          this.infoData.annualSalary = res.value
          break
        case 3:
          this.infoData.singleStatus = res.value
          break
        case 4:
          this.infoData.richesInfo = res.value
          break
      }
    },
    /**
     * @description 保存草稿
     */
    saveDraft () {
    },
    /**
     * @description 关闭提交成功弹窗
     */
    confirm () {
      this.isShowDialog = false
    },
    /**
     * @description 提交用户资料方法
     */
    handleSubmit () {
      this.$root.openLoading()
      this.$http('/api/member/save', this.infoData).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000' && res.data) {
          // TODO: 更新个人信息
          this.updateUserData(this.infoData)
          this.isShowDialog = true
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    }
  },
  mounted () {
    this.infoData = { ...this.infoData, ...this.userData }
  }
}
</script>

