<template>
  <div>
    <van-list
      v-model="isLoading"
      :finished="isFinished"
      finished-text="没有更多了"
      @load="fetchData"
      class="users-list"
    >
      <dt class="users-title">
        <span class="title">我喜欢的人</span>
      </dt>
      <dd class="users-item" v-for="(item, index) in list" :key="index">
        <div class="head-box">
          <div class="img-box">
            <img src="~@/assets/img/default-head.png" alt="" class="img" />
          </div>
          <span class="sentence-num">2</span>
        </div>
        <div class="infomation">
          <p class="name-box"><span class="name">豆子姐</span></p>
          <p class="sentence">你好，请尽快补充个人资料，补充完成后可获得很多很多很多</p>
        </div>
      </dd>
    </van-list>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Like',
  data () {
    return {
      isLoading: false,
      isFinished: false,
      currentPage: 1,
      pageSize: 20,
      list: []
    }
  },
  computed: {
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    fetchData () {
      this.isLoading = true
      this.$http('/api/member/action/list', {
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        currentMemberId: this.userId,
        type: 20
      }, 'get').then(res => {
        this.isLoading = false
        if (res.code === '0000' && res.data) {
          const data = res.data
          if (this.currentPage === 1) {
            this.list = data.data
          } else {
            this.list = [...this.list, ...data.data]
          }
          if (this.currentPage === data.pageCount || !this.userId) {
            this.isFinished = true
          }
          this.currentPage++
        } else {
          this.isFinished = true
          this.$toast(res.msg)
        }
      }, err => {
        this.isFinished = true
        this.isLoading = false
        this.$toast(err)
      })
    }
  }
}
</script>
