<template>
  <div class="upload-cert-container upload-education-container">
    <template v-if="status === 0">
      <div class="upload-tips">拍照上传学历证书</div>
      <div class="photo-item">
        <img v-if="preImg" :src="preImg" alt="" class="img" />
        <img v-else src="~@/assets/img/cert-education.png" alt="点击拍摄毕业证书" class="img"/>
        <input type="file" class="upload-btn" accept="image/*" @change="selectImg"/>
      </div>
      <a href="javascript:;" class="commit common-btn" :class="{disable: !preImg}" @click="commit">提交认证</a>
    </template>
    <template v-else>
      <div class="process">
        <ul class="process-list">
          <li class="process-item">
            <p class="date">2019-01-02 12:30</p>
            <p class="option">你提交了学历认证信息</p>
            <div class="imgs">
              <img src="~@/assets/img/cert-education.png" class="img"/>
              <img src="~@/assets/img/certed.png" alt="已认证" class="certed" v-if="status === 3"/>
            </div>
          </li>
          <li class="process-item">
            <p class="option" v-if="status === 1">待后台审核…</p>
            <template v-if="status === 2">
              <p class="date">2019-01-02 12:30</p>
              <p class="option">后台审核不通过，</p>
              <p class="reason"><span class="strong">不通过原因：</span>照片不清晰</p>
            </template>
            <template v-if="status === 3">
              <p class="date">2019-01-02 12:30</p>
              <p class="option">后台审核通过</p>
            </template>
          </li>
        </ul>
        <a href="javascript:;" class="re-commit common-btn" v-if="status === 2" @click="reCert">重新认证</a>
      </div>
    </template>
  </div>
</template>

<script>
import { isIOS } from '@/utils/tools'

const getRotate = () => {
  return import('@/utils/rotateImg.js')
}
export default {
  name: 'uploadCert',
  data () {
    return {
      // 0: 未上传; 1: 待审核; 2: 未通过; 3: 通过
      status: 0,
      preImg: '',
      selectedImg: ''
    }
  },
  methods: {
    previewImg (file) {
      if (isIOS()) {
        getRotate().then(({ rotatePhoto }) => {
          rotatePhoto(file, (res) => {
            this.setImg(res)
          })
        })
      } else {
        this.setImg(file)
      }
    },
    setImg (file) {
      let url = URL.createObjectURL(file)
      this.preImg = url
      this.selectedImg = file
    },
    selectImg (e) {
      this.previewImg(e.target.files[0])
    },
    commit () {
      if (this.selectedImg) {
        this.status = 2
      } else {
        this.$toast('请先选择照片')
      }
    },
    reCert () {
      this.status = 0
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/constant.scss";
.upload-cert-container.upload-education-container {
  .imgs {
    .img {
      margin-right: 20px;
      width: 230px;
      height: 155px;
    }
  }
}
</style>
