<template>
  <div class="transcation-container">
    <p class="title-con">
      <span class="title">交易记录</span>
      <span class="num">共99条记录</span>
    </p>
    <van-list
      v-model="isLoading"
      :finished="isFinished"
      finished-text="没有更多了"
      @load="fetchData"
      class="list"
    >
      <li class="item" v-for="(item, index) in list" :key="index">
        <p class="name">购买“喜欢” x10</p>
        <p class="price-date">
          <span class="price">19.9元</span>
          <span class="date">2019-09-11 12:12:12</span>
        </p>
      </li>
    </van-list>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'Transcation',
  data () {
    return {
      isLoading: false,
      isFinished: false,
      currentPage: 1,
      pageSize: 20,
      list: []
    }
  },
  computed: {
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    fetchData () {
      this.isLoading = true
      this.$http('/api/member/purchase/list', {
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        currentMemberId: this.userId
      }).then(res => {
        this.isLoading = false
        if (res.code === '0000' && res.data) {
          const data = res.data
          if (this.currentPage === 1) {
            this.list = data.data
          } else {
            this.list = [...this.list, ...data.data]
          }
          if (this.currentPage === data.pageCount || !this.userId) {
            this.isFinished = true
          }
          this.currentPage++
        } else {
          this.isFinished = true
          this.$toast(res.msg)
        }
      }, err => {
        this.isFinished = true
        this.isLoading = false
        this.$toast(err)
      })
    }
  }
}
</script>

