import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router/index.js'
import { http } from '@/utils/http'
import { Toast, List, Dialog, Popup, Picker, DatetimePicker, Area } from 'vant'

Vue.config.productionTip = false
Vue.prototype.$http = http

Vue.use(Toast).use(Dialog).use(List).use(Popup).use(Picker).use(DatetimePicker).use(Area)

window.app = new Vue({
  store,
  router,
  render: h => h(App),
  data () {
    return {
      isLoading: false
    }
  },
  methods: {
    openLoading () {
      this.isLoading = true
    },
    closeLoading () {
      this.isLoading = false
    }
  }
}).$mount('#app')
