<template>
  <van-popup v-model="isShowClip" :lock-scroll="false" position="right" :style="{width: '100%', height: '100%'}">
    <div class="clip-container">
      <MyHeader title="编辑图片" :back="cancel"/>

      <vueCropper
        ref="cropper"
        :img="option.img"
        :autoCrop="option.autoCrop"
        :outputSize="option.size"
        :outputType="option.outputType"
        :fixedBox="option.fixedBox"
        :canMoveBox="option.canMoveBox"
        :fixed="option.fixed"
        :centerBox="option.centerBox"
        :canScale="option.canScale"
        :fixedNumber="option.fixedNumber"
        mode="100% auto"
      ></vueCropper>

      <div class="btns">
        <a href="javascript:;" class="cancel-btn btn" @click="cancel">取消</a>
        <a href="javascript:;" class="done-btn btn" @click="done">完成</a>
      </div>
    </div>
  </van-popup>
</template>
<script>
import MyHeader from '@/components/common/myHeader'
import { VueCropper } from 'vue-cropper'
export default {
  name: 'Clip',
  components: {
    MyHeader,
    VueCropper
  },
  props: {
    isShowClip: Boolean,
    img: {}
  },
  model: {
    prop: 'isShowClip',
    event: 'close'
  },
  watch: {
    img (val) {
      if (val) {
        setTimeout(() => {
          this.covert(val)
        }, 50)
      }
    }
  },
  data () {
    return {
      result: '',
      // 配置参考 vueCropper
      option: {
        img: '',
        size: 1,
        outputType: 'jpeg',
        autoCrop: true,
        fixed: true,
        canScale: true,
        canMoveBox: false,
        fixedBox: true,
        centerBox: true,
        fixedNumber: [1, 0.7]
      }
    }
  },
  methods: {
    covert (file) {
      this.option.img = URL.createObjectURL(file)
      this.$refs.cropper.startCrop()
    },
    cancel () {
      this.$emit('close', false)
    },
    done () {
      this.$refs.cropper.getCropBlob((data) => {
        this.$emit('clipComplete', data)
      })
    }
  }
}
</script>

