<template>
  <van-dialog v-model="isShowDialog" :confirmButtonText="ensureText" @close="close" @confirm="callback" close-on-click-overlay>
    <div class="purchase-modal-content">
      <a href="javascript:;" class="close-btn" @click="close">
        <img class="close-icon" src="~@/assets/img/close.png" alt="" />
      </a>

      <h3 class="title">当前“喜欢”名额不足，请购买</h3>

      <div class="purchase-list">
        <a href="javascript:;"
          class="purchase-item"
          v-for="item in list"
          :key="item.key"
          :class="{'selected' : item.key === selectKey}"
          @click="selectItem(item)"
        >
          <span class="num">{{item.num}}个</span>
          <span class="price">￥{{item.price}}</span>
        </a>
      </div>
    </div>
  </van-dialog>
</template>

<script>
export default {
  name: 'purchaseModal',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isShowDialog: false,
      list: [
        {
          key: 0,
          num: 5,
          price: '19.0'
        },
        {
          key: 1,
          num: 10,
          price: '32.0'
        },
        {
          key: 2,
          num: 20,
          price: '58.0'
        },
        {
          key: 3,
          num: 50,
          price: '138.0'
        }
      ],
      selectKey: 0,
      ensureText: '立即支付'
    }
  },
  watch: {
    show: {
      handler (val) {
        this.isShowDialog = val
      },
      immediate: true
    },
    selectKey: {
      handler (val) {
        const item = this.list.find(_ => {
          return _.key === val
        })

        if (item) {
          this.ensureText = `立即支付 ￥${item.price}`
        }
      },
      immediate: true
    }
  },
  methods: {
    selectItem (item) {
      if (item.key === this.selectKey) {
        return
      }
      this.selectKey = item.key
    },
    close () {
      this.$emit('close')
    },
    cancel () {
      this.$emit('cancel')
    },
    callback () {
      this.$emit('callback')
    }
  }
}
</script>

