<template>
  <div class="test-container">
    <p>test cropper</p>

    <input type="file" id="image-upload" @click="clickInput"/>

    <vueCropper
      v-if="!result"
      ref="cropper"
      :img="option.img"
      :outputSize="option.size"
      :outputType="option.outputType"
      :info="option.info"
      :fixed="option.fixed"
      :fixedBox="option.fixedBox"
      :canMoveBox="option.canMoveBox"
      :centerBox="option.centerBox"
      :canScale="option.canScale"
      :fixedNumber="option.fixedNumber"
    ></vueCropper>

    <img v-else :src="result" alt="" class="result" />

    <a href="javascript:;" class="cancel-btn" @click="cancel">取消</a>
    <a href="javascript:;" class="done-btn" @click="done">完成</a>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper'
export default {
  name: 'cropper',
  components: {
    VueCropper
  },
  data () {
    return {
      result: '',
      // 配置参考 vueCropper
      option: {
        img: '',
        size: 1,
        outputType: 'jpeg',
        info: true,
        fixed: true,
        canScale: true,
        canMoveBox: true,
        centerBox: true,
        fixedNumber: [1, 0.7]
      }
    }
  },
  methods: {
    clickInput () {},
    covert (file) {
      this.option.img = URL.createObjectURL(file)
      this.$refs.cropper.startCrop()
      /* let reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = (e) => {
        this.option.img = e.target.result
        this.$refs.cropper.startCrop()
      } */
    },
    cancel () {
      this.$refs.cropper.clearCrop()
    },
    done () {
      this.$refs.cropper.getCropBlob((data) => {
        this.result = URL.createObjectURL(data)
      })
    }
  },
  watch: {

  },
  mounted () {
    let that = this
    const readFile = function () {
      let file = this.files[0]
      if (!/image\/\w+/.test(file.type)) {
        alert('不是图像类型')
        return
      }
      that.covert(file)
    }
    document.getElementById('image-upload').addEventListener('change', readFile, false)
    // this.$refs.cropper.startCrop()
  }
}
</script>

<style lang="scss">
  .test-container {
    height: 80vh;
    .result {
      width: 100%;
    }
    .vue-cropper {
      background: #fff;
    }
  }
</style>
