          <van-picker :columns="areaColumns" :show-toolbar="true" @change="handleAreachange" @confirm="handleAreaconfirm" @cancel="handleAreacancel"></van-picker>
        </van-popup>

        <purchaseModal :show="isShowModal" @close="isShowModal=false" @callback="purchaseLikeNum"/>

