<template>
  <div class="message-container">
    <dl class="users-list">
      <dt class="users-title">
        <span class="title">消息</span>
        <span class="num">2条未读</span>
      </dt>
      <dd class="users-item" @click="jump('123456')">
        <div class="head-box">
          <div class="img-box">
            <img src="~@/assets/img/default-head.png" alt="" class="img" />
          </div>
          <span class="sentence-num"></span>
        </div>
        <div class="infomation">
          <p class="name-box"><span class="name">豆子姐</span><span class="official">官方</span></p>
          <p class="sentence">你好，请尽快补充个人资料，补充完成后可获得很多很多很多</p>
        </div>
      </dd>
      <dd class="users-item" @click="popup">
        <div class="head-box">
          <div class="img-box">
            <img src="~@/assets/img/default-head.png" alt="" class="img" />
          </div>
          <span class="sentence-num"></span>
        </div>
        <div class="infomation">
          <p class="name-box"><span class="name">豆子姐</span><span class="official">官方</span></p>
          <p class="sentence">你好，请尽快补充个人资料，补充完成后可获得很多很多很多</p>
        </div>
      </dd>
      <dd class="users-item">
        <div class="head-box">
          <div class="img-box">
            <img src="~@/assets/img/default-head.png" alt="" class="img" />
          </div>
        </div>
        <div class="infomation">
          <p class="name-box"><span class="name">豆子姐</span></p>
          <p class="sentence">你好，请尽快补充个人资料，补充完成后可获得很多很多很多</p>
        </div>
      </dd>
      <dd class="users-item">
        <div class="head-box">
          <div class="img-box">
            <img src="~@/assets/img/default-head.png" alt="" class="img" />
          </div>
          <span class="sentence-num"></span>
        </div>
        <div class="infomation">
          <p class="name-box"><span class="name">豆子姐</span><span class="official">官方</span></p>
          <p class="sentence">你好，请尽快补充个人资料，补充完成后可获得很多很多很多</p>
        </div>
      </dd>
    </dl>

    <van-dialog v-model="isShowDialog" :confirmButtonText="dialogConfirmText" @confirm="confirm" @closed="closed">
      <div class="user-message-dialog">
        <p class="tips">对于喜欢你的人，你只须要回点ta一个喜欢，即可免费获得对方的微信号</p>
        <div class="not-show-again" @click="checkKnown">
          <span class="check-box" :class="{'isChecked': notShowAgain}"></span>
          <span class="known">已知悉，不在显示该提示</span>
        </div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import Storage from '@/utils/Storage'
export default {
  name: 'account',
  data () {
    return {
      list: [],
      currentId: undefined,
      isShowDialog: false,
      dialogConfirmText: '我知道了',
      notShowAgain: false
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    jump (id) {
      this.$router.push({
        path: `/message-detail-${id}.html`
      })
    },
    toDetail () {
      this.$router.push({
        path: `/info-${this.currentId}.html`
      })
    },
    popup (id) {
      this.currentId = id
      if (!Storage.load('neverPopMessage')) {
        this.isShowDialog = true
        return
      }

      // 直接跳转到个人详情页
      this.toDetail()
    },
    checkKnown () {
      this.notShowAgain = !this.notShowAgain
    },
    confirm () {
      if (this.notShowAgain) {
        Storage.save('neverPopMessage', true)
      } else {
        Storage.save('neverPopMessage', false)
      }
    },
    closed () {
      // 直接跳转到个人详情页
      this.toDetail()
    }
  },
  mounted () {
    this.$root.openLoading()
    this.$http('/api/member/message', {
      currentMemberId: this.userId
    }).then(res => {
      this.$root.closeLoading()
      if (res.code === '0000') {
        this.list = res.data
      } else {
        this.$toast(res.msg)
      }
    }, err => {
      this.$root.closeLoading()
      this.$toast(err)
    })
  }
}
</script>

