<template>
  <div class="home-outer-container">
    <MyHeader ref="header">
      <div class="btn-con" slot="nav-left"></div>
      <div class="btn-con" slot="nav-right">
        <a href="javascript:;" @click.stop="switchSearchInput" class="header-btn"><img src="~@/assets/img/search.png" class="search-icon" /></a>
      </div>
    </MyHeader>
    <div class="home-container">
      <transition name="scaleHeight">
        <div class="search-con" v-if="isShowSearch">
          <input type="text" v-model="searchId" class="input" placeholder="请输入用户ID查询"/>
          <a href="javascript:;" class="search-btn" @click.stop="fetchInfoById">搜索</a>
        </div>
      </transition>

      <!-- 搜索结果弹窗 -->
      <van-popup v-model="showSearchResult" position="top">
        <div class="searching" v-if="searching">
          <loadingIcon />
        </div>
        <div class="person-item img-box" @click.stop="toInfo(searchResult.id)" v-else-if="searchResult.id">
          <img class="img" src="~@/assets/img/personal.png" />
          <span class="img-num" v-if="searchResult.coverPicList && searchResult.coverPicList.length > 1">{{searchResult.coverPicList.length}}</span>
          <div class="info-box">
            <div class="id-con">
              <span class="name-con">{{searchResult.nickName}}<img v-if="searchResult.verifyStatus" class="identify-icon" src="~@/assets/img/identify.png"/></span>
              <span class="id">ID: {{searchResult.id}}</span>
            </div>
            <p class="info-con desc">{{searchResult.age ? `${searchResult.age}岁 ` : ''}} {{searchResult.high ? `${searchResult.high}cm ` : ''}}{{searchResult.education}} {{searchResult.job}}{{searchResult.company ?`（${searchResult.company}）`: ''}}</p>
            <p class="info-con location">{{searchResult.locationCity ? `现居${searchResult.locationCity}` : ''}}{{searchResult.locationCity && searchResult.nativeLocation ? ' · ' : ''}}{{searchResult.nativeLocation ? `${searchResult.nativeLocation}人` : ''}}</p>
          </div>
        </div>
        <div class="no-searchResult" v-else>
          <img src="~@/assets/img/no-search-result.png" alt="" class="no-result-img" />
          <p class="no-result-tips">未搜索到对应用户</p>
        </div>
      </van-popup>

      <router-link to="/combo.html" class="combo-link common-bg">
        <span class="con-left"><img class="stamp" src="~@/assets/img/bitmap.png"/>1对1红娘套餐，服务更精准，脱单更高效</span>
        <img class="arrow-right" src="~@/assets/img/arrow-right.png"/>
      </router-link>

      <van-list
        v-model="isLoading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="person-list"
      >
        <li class="person-item img-box" v-for="item in list" :key="item.id" @click.stop="toInfo(item.id)">
          <img class="img" src="~@/assets/img/personal.png" />
          <span class="img-num" v-if="item.coverPicList && item.coverPicList.length > 1">{{item.coverPicList.length}}</span>
          <div class="info-box">
            <div class="id-con">
              <span class="name-con">{{item.nickName}}<img v-if="item.verifyStatus" class="identify-icon" src="~@/assets/img/identify.png"/></span>
              <span class="id">ID: {{item.id}}</span>
            </div>
            <p class="info-con desc">{{item.age ? `${item.age}岁 ` : ''}} {{item.high ? `${item.high}cm ` : ''}}{{item.education}} {{item.job}}{{item.company ?`（${item.company}）`: ''}}</p>
            <p class="info-con location">{{item.locationCity ? `现居${item.locationCity}` : ''}}{{item.locationCity && item.nativeLocation ? ' · ' : ''}}{{item.nativeLocation ? `${item.nativeLocation}人` : ''}}</p>
          </div>
        </li>
      </van-list>
    </div>

    <van-popup v-model="completeInfo" position="bottom" :overlay="false" :close-on-click-overlay="false">
      <div class="complete-info-pop">
        <p class="tips">真诚、严肃、平等交友<br/>查看更多优质会员，请先完善个人资料</p>
        <a href="javascript:;" class="jump-btn" @click="jump">{{buttonText}}</a>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import MyHeader from '@/components/common/myHeader'
import loadingIcon from '@/components/common/loadingIcon'

export default {
  name: 'Home',
  components: {
    MyHeader,
    loadingIcon
  },
  data () {
    return {
      isShowSearch: false,
      searchId: '',
      searching: false,
      showSearchResult: false,
      hadResult: true,
      searchResult: {},
      isLoading: false,
      finished: false,
      list: [],
      currentPage: 1,
      pageSize: 20,
      // 完善资料弹窗
      completeInfo: false
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    buttonText () {
      let msg = ''
      if (!this.userId) {
        msg = '立即登录'
      } else if (this.userData.verifyStatus !== 3) {
        msg = '立即完善个人资料'
      }
      return msg
    }
  },
  watch: {
    showSearchResult (val) {
      if (val) {
        this.$refs['header'].$el.style.zIndex = 3000
      } else {
        setTimeout(() => {
          this.$refs['header'].$el.style.zIndex = 99
        }, 500)
      }
    }
  },
  methods: {
    switchSearchInput () {
      this.isShowSearch = !this.isShowSearch
      if (this.isShowSearch) {
        this.$nextTick(() => {
          document.querySelector('.input').focus()
        })
      }
    },
    onLoad () {
      this.$http('/api/member/list', {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }, 'get').then(res => {
        this.isLoading = false
        this.finished = true
        if (res.code === '0000') {
          const data = res.data
          if (this.currentPage === 1) {
            this.list = data.data
          } else {
            this.list = [...this.list, ...data.data]
          }
          if (this.currentPage === data.pageCount || !this.userId || parseInt(this.userData.verifyStatus) !== 3) {
            this.finished = true
          } else {
            this.currentPage++
          }
        } else {
          this.$toast(res.data.msg)
        }
      }, () => {
        this.finished = true
        this.isLoading = false
      })
    },
    fetchInfoById () {
      if (!/^\d{2,6}$/.test(this.searchId)) {
        this.$toast('请输入正确的用户id')
        return
      }
      this.searching = true
      this.showSearchResult = true
      this.$http('/api/member/info', {
        targetMemberId: this.searchId
      }, 'get').then(res => {
        this.searching = false
        if (res.code === '0000') {
          this.searchResult = res.data
        } else {
          this.$toast(res.data.msg)
        }
      }, () => {
        this.searching = false
      })
    },
    toInfo (id) {
      this.$router.push({
        path: `/info-${id}.html`
      })
    },
    jump () {
      this.completeInfo = false
      this.$router.push({
        path: '/account.html'
      })
    },
    scroll () {
      if (window.innerHeight + window.pageYOffset + 100 >= document.body.offsetHeight) {
        this.completeInfo = true
      }
    }
  },
  activated () {
    if (!this.userId || parseInt(this.userData.verifyStatus) !== 3) {
      window.addEventListener('scroll', this.scroll)
    }
  },
  beforeRouteLeave (to, from, next) {
    this.completeInfo = false
    window.removeEventListener('scroll', this.scroll)
    next()
  }
}
</script>
