<template>
  <div class="upload-cert-container">
    <template v-if="status === 0">
      <div class="upload-tips">拍照上传身份证正反面</div>
      <div class="photo-item">
        <img v-if="preUploadFront" :src="`/api/member/identity/pic?id=${preUploadFront}&currentMember=${userId}&targetMember=${userId}`" alt="" class="img" />
        <img v-else src="~@/assets/img/cert-front.png" alt="点击拍摄身份证人像面" class="img"/>
        <input type="file" class="upload-btn" accept="image/*" @change="selectFront"/>
      </div>
      <div class="photo-item">
        <img v-if="preUploadBack" :src="`/api/member/identity/pic?id=${preUploadBack}&currentMember=${userId}&targetMember=${userId}`" alt="" class="img" />
        <img v-else src="~@/assets/img/cert-back.png" alt="点击拍摄身份证国徽面" class="img"/>
        <input type="file" class="upload-btn" accept="image/*" @change="selectBack"/>
      </div>
      <a href="javascript:;" class="commit common-btn" :class="{disable: !preUploadFront || !preUploadBack}" @click="commit">提交认证</a>
    </template>
    <template v-else>
      <div class="process">
        <ul class="process-list">
          <li class="process-item">
            <p class="date">2019-01-02 12:30</p>
            <p class="option">你提交了身份认证信息</p>
            <div class="imgs">
              <img src="~@/assets/img/cert-front.png" class="img"/>
              <img src="~@/assets/img/cert-front.png" class="img"/>
              <img src="~@/assets/img/certed.png" alt="已认证" class="certed" v-if="status === 3"/>
            </div>
          </li>
          <li class="process-item">
            <p class="option" v-if="status === 1">待后台审核…</p>
            <template v-if="status === 2">
              <p class="date">2019-01-02 12:30</p>
              <p class="option">后台审核不通过，</p>
              <p class="reason"><span class="strong">不通过原因：</span>照片不清晰</p>
            </template>
            <template v-if="status === 3">
              <p class="date">2019-01-02 12:30</p>
              <p class="option">后台审核通过</p>
            </template>
          </li>
        </ul>
        <a href="javascript:;" class="re-commit common-btn" v-if="status === 2" @click="reCert">重新认证</a>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { isIOS, transferBytes } from '@/utils/tools'

const getRotate = () => {
  return import('@/utils/rotateImg.js')
}

export default {
  name: 'uploadCert',
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  data () {
    return {
      // 0: 未上传; 1: 待审核; 2: 未通过; 3: 通过
      status: 0,
      preUploadFront: '',
      preUploadBack: '',
      identifyPics: []
    }
  },
  methods: {
    handleImg (file, type, target) {
      if (isIOS()) {
        getRotate().then(({ rotatePhoto }) => {
          rotatePhoto(file, (res) => {
            this.uploadImg(res, type, target)
          })
        })
      } else {
        this.uploadImg(file, type, target)
      }
    },
    uploadImg (file, type, target) {
      this.$root.openLoading()
      // 将文件转换成bytes后上传
      transferBytes(file).then(res => {
        this.$http('/api/member/upload/pic', {
          currentMemberId: this.userId,
          type: 2,
          pic: res
        }).then(res => {
          this.$root.closeLoading()
          if (res.code === '0000') {
            if (type === 0) {
              this.preUploadFront = res.data
            } else {
              this.preUploadBack = res.data
            }
          } else {
            this.$toast(res.msg)
            target.value = ''
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
          target.value = ''
        })
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
        target.value = ''
      })
    },
    selectFront (e) {
      let file = e.target.files[0]
      this.handleImg(file, 0, e.target)
    },
    selectBack (e) {
      let file = e.target.files[0]
      this.handleImg(file, 1, e.target)
    },
    commit () {
      if (this.preUploadFront && this.preUploadBack) {
        this.identifyPics = [this.preUploadFront, this.preUploadBack]
        this.$root.openLoading()
        this.$http('/api/member/save', {
          currentMemberId: this.userId,
          identityPicIds: this.identifyPics
        }).then(res => {
          this.$root.closeLoading()
          if (res.code === '0000' && res.data) {
            // TODO: 更新个人信息
            this.status = 1
          } else {
            this.$toast(res.msg)
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
        })
      }
    },
    reCert () {
      this.status = 0
    }
  }
}
</script>
