// 微信工具类，集成登录、支付功能
export default {
  init: function ({ appid, redirectUri, state }) {
    this.appid = appid
    this.redirectUri = encodeURIComponent(redirectUri)
    this.state = state || 'defaultState'
    return this
  },
  author: function () {
    if (!this.redirectUri) throw new Error('缺失参数：redirectUri')
    let url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${this.appid}&redirect_uri=${this.redirectUri}&response_type=code&scope=snsapi_userinfo&state=${this.state}#wechat_redirect&connect_redirect=1
=1`
    // console.error(url)
    window.location.href = url
  },
  getUrlParams: function () {
    let url = location.search
    // eslint-disable-next-line no-new-object
    let urlParams = new Object()
    if (url.indexOf('?') !== -1) {
      let str = url.substr(1); let strs = str.split('&')
      for (let i = 0; i < strs.length; i++) urlParams[strs[i].split('=')[0]] = unescape(strs[i].split('=')[1])
    }
    return urlParams
  },
  wexinPay (data, initConfig) {
    let { appId, timeStamp, nonceStr, packageValue, paySign, signType } = data
    return new Promise((resolve, reject) => {
      const jsApiCall = () => {
        // eslint-disable-next-line no-undef
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            'appId': appId,
            'nonceStr': nonceStr,
            'package': packageValue,
            'signType': signType,
            'timeStamp': timeStamp,
            'paySign': paySign
          },
          function (res) {
            // eslint-disable-next-line no-undef
            WeixinJSBridge.log(res.err_msg)
            if (res.err_msg === 'get_brand_wcpay_request:ok') {
              resolve({ actionCode: 200 })
            } else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
              resolve({ actionCode: 201 })
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject()
            }
          }
        )
      }
      if (typeof WeixinJSBridge === 'undefined') {
        if (document.addEventListener) {
          document.addEventListener('WeixinJSBridgeReady', jsApiCall, false)
        } else if (document.attachEvent) {
          document.attachEvent('WeixinJSBridgeReady', jsApiCall)
          document.attachEvent('onWeixinJSBridgeReady', jsApiCall)
        }
      } else {
        jsApiCall()
      }
    })
  }
}
